@import url(https://fonts.googleapis.com/css2?family=PT+Serif&display=swap);
body {
  margin: 0;
  font-family: "PT Serif", serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.text {
  word-spacing: 0.1em;
  line-height: 2em;
}

.image {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 80%;
  height: auto;
}

.vid {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 80%;
}

.image-container {
  max-width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
}

.logo {
  height: 38px;
}

.buttons {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.top-bar {
  border-bottom: solid;
  border-bottom-width: 0.1em;
  background-color: white;
  position: relative;
}

